import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {AuthService} from '../../services/auth.service';
import {Constants} from '../../app.constants';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss'],
  providers: [AuthService],
  encapsulation: ViewEncapsulation.None
})
export class ResetPasswordComponent implements OnInit, AfterViewInit {

  model = {
    email: '',
    password: '',
    password_confirmation: ''
  };
  loading = false;
  processing = false;
  errorMessage: any [];
  linkError: string;
  resetToken = this.route.snapshot.params['reset_token']

  constructor(private authService: AuthService,
              private route: ActivatedRoute,
              private _Location: Location,
              private router: Router) {
  }

  ngOnInit() {
    this.loading = true;
    this.authService.checkResetToken(this.resetToken)
      .subscribe(resetToken => {
          if (resetToken) {
            this.loading = false;
          }
        },
        (err) => {
          this.linkError = Constants.handleError(err, this.router);
          this.loading = false
        }
      )
  }

  onSubmit() {
    this.processing = true;
    this.authService.changePassword({
      email: this.model.email,
      password: this.model.password,
      password_confirmation: this.model.password,
      token: this.resetToken
    }).subscribe(password => {
        if (password) {
          this.router.navigate(['/']);
        } else {
          this.processing = false;
        }
      }, (err) => {
        Constants.handleError(err, this.router, true);
        this.processing = false;
      }
    );
  }

  showPassword(input: any): any {
    input.type = input.type === 'password' ? 'text' : 'password';
  }

  ngAfterViewInit() {
    document.getElementById('preloader').classList.add('hide');
  }

}
