import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/throw'
import 'rxjs/add/operator/catch'
import 'rxjs/add/operator/map'
import {DocumentsFormat} from '../interfaces/interfaces';
import {AuthService} from './auth.service';
import {HttpClient} from '@angular/common/http';
import {Constants} from '../app.constants';

@Injectable()
export class DocumentFormatService {

    constructor(public http: HttpClient, private authService: AuthService) {
    }

    getDocumentFormats(filter: any = ''): Observable<any> {
        return this.http.get(Constants.url + 'doc-formats?filter' + filter, Constants.getAuthHeaderOption());
    }

    getAllowedDocumentFormats(): Observable<any> {
        return this.http.get(Constants.url + 'doc-formats/allowed-formats', Constants.getAuthHeaderOption());
    }

    getDocumentFormatByID(id: number): Observable<any> {
        const url = Constants.url + 'doc-formats'
        return this.http.get(url + '/' + id, Constants.getAuthHeaderOption());
    }

    addDocFormat(document_format: DocumentsFormat): Observable<any> {
        const url = Constants.url + 'doc-formats';
        return this.http.post(url, document_format, Constants.getAuthHeaderOption());
    }

    allowDocFormat(document_format: DocumentsFormat): Observable<any> {
        const url = Constants.url + 'doc-formats/allow-marked';
        return this.http.post(url, document_format, Constants.getAuthHeaderOption());
    }


    updateDocumentFormat(document_format: DocumentsFormat): Observable<any> {
        const url = Constants.url + 'doc-formats';
        return this.http.put(url + '/' + document_format.id, document_format, Constants.getAuthHeaderOption());
    }

    deleteDocumentFormat(id: number): Observable<any> {
        const url = Constants.url + 'doc-formats';
        return this.http.delete(url + '/' + id, Constants.getAuthHeaderOption());
    }

}
