import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import 'rxjs/add/operator/map';
import {Constants} from '../../app.constants';
import {AuthService} from '../../services/auth.service';


@Component({
    selector: 'app-activation-component',
    templateUrl: './activation-component.component.html',
    styleUrls: ['./activation-component.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class ActivationComponent implements OnInit, AfterViewInit {
    errorMessage: string;
    loading = true;

    constructor(private authService: AuthService,
                private route: ActivatedRoute,
                private router: Router) {
    }

    ngOnInit() {
        const token = this.route.snapshot.params['activation_token'];
        this.activateUserAccount(token);
    }

    activateUserAccount(token: string) {
        this.authService.activateUserAccount(token)
            .subscribe(result => {
                    if (result) {
                        this.loading = false;
                        this.router.navigate(['/activated'])
                    }
                }, (err) => {
                    this.loading = false;
                    this.errorMessage = Constants.handleError(err, this.router);
                    //      console.log(this.errorMessage);
                })
    }

    ngAfterViewInit() {
        document.getElementById('preloader').classList.add('hide');
    }

}
