import {ChatService} from './services/chat.service';
import {ChatStatus} from './data';
import {formatDate} from '@angular/common';
import {ChatGroup, ChatInstance, ChatMessage, IPicture, IUser} from './interfaces/interfaces';
import {Constants} from './app.constants';
import {UploadService} from './services/upload.service';
import {Router} from '@angular/router';
import * as uuid from 'uuid';

export class ChatExtra {
  contentView: any;
  message = '';
  upload: any = false;
  uploadProgress = 0;
  nextUrl: any = null;
  nextId = 0;

  messageToForward: null;
  selectedGroups: ChatInstance[] = [];

  showEmojiPicker = false;
  fetchingMessagesError = '';
  lastScrollPosition: any = 0;
  replyTo = null;

  group: ChatGroup;
  // messages: ChatMessage[] = [];
  show = false;
  isFake = true;
  obj?: any;
  isInit = false;
  unreadCount?: any = 0;
  fetchingMessages = false;
  uploadId = '';
  errorMessage = '';

  constructor(private chatService: ChatService,
              private router: Router,
              private uploadService: UploadService,
              group: ChatGroup) {
    this.group = group;
    // console.log(group);
    // this.uploadId = 'chat-message-' + Constants.grabRandomID();
    this.uploadId = 'chat-instance-message-' + Constants.grabRandomID();
    // this.listenForGroupMessage();
  }

  public get presentUser() {
    return this.chatService.presentUser;
  }


  public get status() {
    return this.chatService.connectionStatus || ChatStatus.neutral;
  }

  public get messages() {
    return this.chatService.getAllCurrentMessages.filter(mes => mes.group_id === this.group.id);
  }

  public get allMessages() {
    return this.chatService.getAllCurrentMessages;
  }

  public fetchMessages(url = '') {
    // this.fetchingMessages = true;
    // this.fetchingMessagesError = '';
    this.chatService.fetchMessages(this.group.id, url).subscribe((data: any) => {
      (data.data || []).forEach(mes => {
        mes.was_sent = true;
        this.allMessages.push(mes);
      });
      if (this.messages.length) {
        const lastId = this.messages[this.messages.length - 1].id;
        this.scrollToView(lastId);
      }
      this.nextUrl = data.next_url;
      this.fetchingMessages = false;
    }, err => {
      this.fetchingMessages = false;
      this.fetchingMessagesError = Constants.handleError(err, this.router);
    });
  }

  removeUser(user) {

  }

  get last_message() {
    const length = this.sortedMessages.length;
    return !length ? null : this.sortedMessages[length - 1];
  }

  get user() {
    return this.group;
  }

  get chatStatuses() {
    return ChatStatus;
  }

  get chatStatus() {
    return this.chatService.getConnectionStatus;
  }

  isToday(someDate) {
    const today = new Date();
    return someDate.getDate() === today.getDate() &&
      someDate.getMonth() === today.getMonth() &&
      someDate.getFullYear() === today.getFullYear();
  }

  isYesterday(someDate) {
    const today = new Date();
    today.setDate(today.getDate() - 1);
    return someDate.getDate() === today.getDate() &&
      someDate.getMonth() === today.getMonth() &&
      someDate.getFullYear() === today.getFullYear();
  }

  toggleEmojiPicker() {
    this.showEmojiPicker = !this.showEmojiPicker;
  }

  setReplyToAs(original: ChatMessage) {
    if (!original) {
      this.replyTo = null;
      return;
    }
    const mes: ChatMessage = {
      file: original.file,
      message: original.message,
      was_sent: original.was_sent,
      time: original.time,
      upload: original.upload,
      time_created: original.time_created,
      id: original.id,
      status: original.status,
      content: original.content,
      is_incoming: original.is_incoming,
      created_at: original.created_at,
      created_by: original.created_by,
      by: original.by
    };

    if (mes.file) {
      mes.message = `<i class="fa fa-cloud-download"></i> <i>${mes.file.seo_filename}</i>`;
    } else {
      const doc = document.createElement('div');
      doc.innerHTML = mes.message;
      mes.message = doc.textContent;
    }

    mes.content = mes.message;

    this.replyTo = mes;
  }

  forwardMessage(mes) {
    this.selectedGroups = [];
    this.messageToForward = mes;
  }

  doForward() {
    document.getElementById('close-modal-button-' + this.group.id).click();
    if (!this.messageToForward) {
      return;
    }
    const message = this.messageToForward;
    const list = this.selectedGroups;
    list.forEach(group => {
      group.sendForwardedMessage(message);
    });

    this.messageToForward = null;
    this.selectedGroups = [];
  }

  isPicture(file: IPicture) {
    return this.chatService.isPicture(file);
  }

  isVideo(file: IPicture) {
    return this.chatService.isVideo(file);
  }

  isPdf(file: IPicture) {
    return this.chatService.isPdf(file);
  }

  getTime(mes: ChatMessage) {
    const date = new Date(mes.time);

    if (this.isToday(date)) {
      return formatDate(date, 'HH:mm', 'en-US', '1');
    } else if (this.isYesterday(date)) {
      return 'Yest. ' + formatDate(date, 'HH:mm', 'en-US', '1');
    }

    return formatDate(date, 'dd/MM/yy HH:mm', 'en-US', '1');
  }

  addEmoji(event) {
    this.message += `${event.emoji.native}`;
    // this.showEmojiPicker = false;
  }

  getMessageSender(mes: ChatMessage) {
    const creators = this.group.participants.filter(p => p.id.toString() === mes.by.toString());
    if (creators.length) {
      const creator = creators[0];
      if (creator.is_vendor) {
        return creator.vendor ? creator.vendor.business_name : creator.full_name;
      } else {
        return creator.full_name;
      }
    }
    return 'Anonymous';
  }

  scrollToView(id?: any) {
    setTimeout(() => {
      const ele = document.getElementById(id);

      if (!ele) {
        return this.scrollToView(id);
      }

      // for mobile app
      // if (this.contentView) {
      //     this.contentView.scrollToBottom(300);
      // }
      // this.contentView.scrollTo(0, dimensions.scrollHeight+100, 100);

      const element = jQuery('#' + id);
      const selector = '.message_area.' + this.group.id;
      element.removeClass('momentary-highlight');
      const scroller = jQuery(selector);
      scroller.scrollTop(scroller.scrollTop() + element.position().top);

      element.addClass('momentary-highlight');
    }, 500);
  }

  get hasReplyTo() {
    return this.replyTo !== null;
  }

  get sortedMessages() {
    return this.messages.sort((a: ChatMessage, b: ChatMessage) => {
      return (<any>(new Date(a.time)) - <any>(new Date(b.time)));
    });
  }

  get statuses() {
    return ChatStatus;
  }

  public uploadFinished(event: any) {
    this.upload = event;
    this.sendMessage();

    this.uploadProgress = 0;
  }

  updateUploadStatus(event: any) {
    if (event.type === 'error') {
      this.upload = false;
      this.uploadProgress = 0;
      //    cancel upload here
    } else if (event.type === 'progress') {
      this.uploadProgress = event.value;
    }
  }

  triggerUpload(id) {
    this.upload = false;
    this.uploadProgress = 0;
    // start upload here

    // alert(id + ': ' + this.uploadId);
    if (id === this.uploadId) {
      this.uploadService.upload(id);
    }
  }

  prepareMessage(message: string) {
    const messages = message.split('\n');
    return messages.join('<br>');
  }

  sendMessage() {
    const message = this.prepareMessage(this.message);

    if (!message && !this.upload) {
      return;
    }
    const date = new Date();
    const mes = <ChatMessage>{
      by: (this.chatService.presentUser || {i: ''}).i,
      time_created: (new Date()).getTime(),
      time: date.toISOString(),
      message: message,
      group_id: this.group.id,
      content: message,
      status: 'pending',
      file: this.upload,
      was_sent: false,
      relying_to: this.replyTo,
      signature: uuid.v4(),
      id: 'fake_' + Math.floor(Math.random() * 100000).toString() + Math.floor(Math.random() * 10000).toString(),
    };
    this.allMessages.push(mes);
    // this.chatEmitter.publish('new-chat-message', mes);

    this.message = '';
    const u = this.upload;
    this.upload = false;

    this.scrollToView(mes.id);

    const dataToSend = {
      message: mes.message,
      upload: u ? u.id : '',
      group_id: this.group.id,
      replying_to: this.hasReplyTo ? this.replyTo.id : null,
      signature: mes.signature
    };
    this.replyTo = null;
    this.chatService.sendMessage(dataToSend).subscribe(data => {
      // if (data.data) {
      //     data.data.time_created = mes.time_created;
      //     data.data.was_sent = true;
      //     const old = this.chatService.allCurrentMessages.findIndex(x => x.signature === mes.signature && x.id === mes.id);
      //     if (old > -1) {
      //         this.chatService.allCurrentMessages[old] =
      //     }
      //     this.messages[this.messages.indexOf(mes)] = data.data;
      // }
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
    });
  }

  sendForwardedMessage(forwardedMessage: ChatMessage) {
    const message = this.prepareMessage(forwardedMessage.message);

    if (!message && !this.upload) {
      return;
    }
    const date = new Date();
    const mes = <ChatMessage>{
      by: (this.chatService.presentUser || {i: ''}).i,
      time_created: (new Date()).getTime(),
      time: date.toISOString(),
      message: message,
      content: message,
      status: 'pending',
      file: forwardedMessage.file,
      was_sent: false,
      relying_to: null,
      id: 'fake_' + Math.floor(Math.random() * 100000).toString() + Math.floor(Math.random() * 10000).toString(),
    };
    this.allMessages.push(mes);
    this.scrollToView(mes.id);

    const dataToSend = {
      message: mes.message,
      upload: forwardedMessage.upload ? forwardedMessage.upload.id : '',
      group_id: this.group.id,
      replying_to: null,
      signature: mes.signature
    };
    this.chatService.sendMessage(dataToSend).subscribe(data => {
      // if (data.data) {
      //     data.data.time_created = mes.time_created;
      //     data.data.was_sent = true;
      //     this.messages[this.messages.indexOf(mes)] = data.data;
      // }
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
    });
  }

  get users(): IUser[] {
    return this.group.participants.filter(user => !user.is_vendor).sort((a: IUser, b: IUser) => {
      const aName = a.full_name.toLowerCase(), bName = b.full_name.toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }

  get vendors() {
    return this.group.participants.filter(user => user.is_vendor).sort((a: IUser, b: IUser) => {
      const aName = (a.vendor ? a.vendor.business_name : a.full_name).toLowerCase(),
        bName = (b.vendor ? b.vendor.business_name : b.full_name).toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }
}
